// 一级导航栏
<template>
  <div id="oneBar" ref="oneBar">
    <div
      class="item"
      v-for="(item, i) in menuList"
      :key="i"
      :class="{ active: i === index }"
      @touchend="scrollTo(i, $event, item)"
      @touchstart="move = false"
      @touchmove="move = true"
    >
      <div class="img-box">
        <img :src="item.imgURL" />
      </div>
      <div class="title">{{ item.title }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      move: false,
    };
  },
  props: ['menuList'],
  methods: {
    scrollTo(i, e, item) {
      // console.log(e);
      if (this.move) {
        return;
      }

      this.index = i;

      const itemWidth = e.target.offsetWidth;
      const itemLeft = e.target.getBoundingClientRect().left;
      const { oneBar } = this.$refs;
      const barWidth = oneBar.offsetWidth;

      oneBar.scrollLeft += itemWidth / 2 + itemLeft - barWidth / 2;

      this.$store.dispatch('setSidebar', { type: item.title });
    },
  },
};
</script>

<style scoped lang="less">
#oneBar {
  width: 375px;
  height: 104px;
  display: flex;
  overflow: auto;
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    width: 0;
    background: none;
  }

  .item {
    width: 50px;
    height: 70px;
    padding: 10px 5px;

    .img-box {
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 2px solid #fff;
      border-radius: 23px;

      img {
        width: 45px;
        height: 45px;
        border-radius: 50%;
      }
    }

    .title {
      text-align: center;
      font-size: 12px;
      margin-top: 5px;
    }

    &.active {
      .img-box {
        border-color: #d13193;
      }
      .title {
        color: #fff;
        background-color: #d13193;
        font-weight: bold;
        border-radius: 30px;
      }
    }
  }
}
</style>
