// 二级导航
<template>
  <div id="sidebar" ref="sidebar">
    <div
      class="item"
      v-for="(item, i) in sideList"
      :key="i"
      :class="{ active: i === index }"
      @touchend="scrollTo(i, $event, item)"
      @touchstart="move = false"
      @touchmove="move = true"
    >
      {{ i === 0 ? "全部" : item }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      move: false,
      //   sideList: [],
    };
  },
  computed: {
    sideList() {
      return this.$store.state.sidebar;
    },
  },
  methods: {
    scrollTo(i, e, item) {
      if (this.move) {
        return;
      }
      this.index = i;

      const itemHeight = e.target.offsetHeight;
      const itemTop = e.target.getBoundingClientRect().top;
      const { sidebar } = this.$refs;
      const sidebarTop = sidebar.offsetTop;
      const sidebarHeight = sidebar.offsetHeight;

      sidebar.scrollTop
        += itemTop + itemHeight / 2 - sidebarTop - sidebarHeight / 2;

      this.$store.commit('resetGoodsList');
      this.$store.dispatch('setGoodsList', { type: item, page: 1 });
    },
  },
  destroyed() {
    // console.log('组件被销毁了');
    // this.$store.commit('resetGoodsList');
  },
};
</script>

<style lang="less">
#sidebar {
  position: fixed;
  left: 0;
  top: 155px;
  bottom: 50px;
  width: 79px;
  overflow: auto;
  background: #f8f8f8;
  scroll-behavior: smooth;
  &::-webkit-scrollbar {
    width: 0;
    background: none;
  }

  .item {
    width: 79px;
    font-size: 12px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    position: relative;

    &.active {
      font-weight: bold;
      color: #ff1a90;

      &::after {
        content: "";
        position: absolute;
        width: 6px;
        height: 18px;
        background: #ff1a90;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
      }
    }
  }
}
</style>
