import Vue from 'vue';
import com from './index.vue';

const Animate = Vue.extend(com);

export default function ({
  startX,
  startY,
  endX,
  endY,
  src,
  width,
  height,
}) {
  const app = new Animate({
    el: document.createElement('div'),
    data() {
      return {
        moveX: startX,
        moveY: startY,
        sx: 1,
        sy: 1,
        opacity: 1,
        exist: true, // 用来判断是否展示的，方便后面来销毁组件
        src,
        width,
        height,
      };
    },
  });

  // 插入到全局 DOM 中
  document.body.appendChild(app.$el);

  // 用于变化样式
  setTimeout(() => {
    app.moveX = endX;
    app.moveY = endY;
    app.sx = 0.1;
    app.sy = 0.1;
    app.opacity = 0;
  }, 0);

  // 销毁组件
  setTimeout(() => {
    app.exist = false;
  }, 1100);
}
