// 商品列表
<template>
  <div id="list">
    <div class="header van-hairline--top-bottom">
      <span :class="{ active: type === 'all' }" @click="changeType('all')"
        >综合</span
      >
      <span :class="{ active: type === 'sale' }" @click="changeType('sale')"
        >销量</span
      >
      <span
        class="price"
        :class="{
          'price-up': type === 'price-up',
          'price-down': type === 'price-down',
        }"
        @click="changeType('price')"
        >价格</span
      >
    </div>

    <div class="list-container">
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          :finished-text="msg"
          @load="onLoad"
          :immediate-check="false"
        >
          <template v-for="(item, index) in $store.state.goodsList">
            <Card :key="index" :data="item" />
          </template>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>

<script>
import Card from '@/components/Card.vue';

export default {
  data() {
    return {
      type: 'all',
      loading: false,
      finished: false,
      refreshing: false,
      page: 1,
      msg: '上拉查看下一个分类',
    };
  },
  components: {
    Card,
  },
  watch: {
    '$store.state.goodsList': function (val) {
      // console.log(val);
      // console.log(this.$store.state.total);

      if (val.length >= this.$store.state.total) {
        this.finished = true;
        this.page = 1;
      } else {
        this.finished = false;
      }
    },
  },
  methods: {
    onLoad() {
      // 底下加载更多
      // console.log(this.refreshing);
      // if (this.refreshing) {
      //   console.log('onLoad');
      //   return;
      // }
      this.page += 1;
      // console.log('List');
      this.$store.dispatch('setGoodsList', { page: this.page });
      this.loading = false;
    },
    async onRefresh() {
      // 下拉重新加载数据
      // console.log(this.refreshing);
      // 清空列表数据
      // this.refreshing = true;
      // this.finished = true;
      this.page = 1;
      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      // this.loading = true;
      this.$store.commit('resetGoodsList');
      // console.log('onRefresh');
      await this.$store.dispatch('setGoodsList', {
        page: this.page,
        sort: this.type,
      });
      this.refreshing = false;
      this.loading = false;
    },
    changeType(val) {
      if (val === 'price') {
        if (this.type === 'price-up') {
          this.type = 'price-down';
        } else {
          this.type = 'price-up';
        }
      } else {
        this.type = val;
      }
      this.onRefresh();
    },
  },
};
</script>

<style lang="less">
@import url("~@/assets/css/list.less");

.van-pull-refresh {
  overflow: visible !important;
}
</style>
